.tu-date-editor {
  position: relative;
  display: inline-block;
  text-align: left; }
  .tu-date-editor.tu-input ::selection, .tu-date-editor.tu-input__inner ::selection {
    transition: all 0.3s ease;
    color: #6d5dfc; }
  .tu-date-editor--dates .tu-input__inner {
    text-overflow: ellipsis;
    white-space: nowrap; }
  .tu-date-editor.is-focus {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-date-editor .tu-input__icon--suffix {
    cursor: pointer;
    transition: all 0.3s; }
    .tu-date-editor .tu-input__icon--suffix:hover {
      color: #9baacf; }
  .tu-date-editor .tu-range__icon {
    font-size: 14px;
    margin-left: -5px;
    color: #bec8e4;
    float: left;
    line-height: 32px; }
  .tu-date-editor .tu-range-input {
    appearance: none;
    border: none;
    outline: none;
    display: inline-block;
    height: 100%;
    margin: 0;
    padding: 0;
    width: 39%;
    text-align: center;
    font-size: 14px;
    color: #9baacf;
    background: transparent; }
    .tu-date-editor .tu-range-input::placeholder {
      color: #bec8e4; }
  .tu-date-editor .tu-range-separator {
    display: inline-block;
    height: 100%;
    padding: 0 5px;
    margin: 0;
    text-align: center;
    line-height: 32px;
    font-size: 14px;
    width: 5%;
    color: #bec8e4; }
  .tu-date-editor .tu-range__close-icon {
    font-size: 14px;
    color: #bec8e4;
    width: 25px;
    display: inline-block;
    float: right;
    line-height: 32px;
    cursor: pointer; }
    .tu-date-editor .tu-range__close-icon:hover {
      color: #9baacf; }

.tu-picker-panel {
  border-radius: 4px;
  line-height: 30px;
  margin: 5px 0;
  color: #9baacf;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-picker-panel__body::after, .tu-picker-panel__body-wrapper::after {
    content: "";
    display: table;
    clear: both; }
  .tu-picker-panel__content {
    position: relative;
    margin: 15px; }
  .tu-picker-panel__footer {
    border-top: 1px solid rgba(190, 200, 228, 0.38);
    text-align: right;
    padding: 4px;
    text-align: right;
    position: relative;
    font-size: 0; }
  .tu-picker-panel__shortcut {
    display: block;
    width: 100%;
    border: 0;
    background-color: transparent;
    line-height: 28px;
    font-size: 12px;
    color: #9baacf;
    padding-left: 12px;
    text-align: left;
    outline: none;
    cursor: pointer;
    transition: all 0.3s; }
    .tu-picker-panel__shortcut:hover {
      color: #6d5dfc; }
    .tu-picker-panel__shortcut.active {
      color: #6d5dfc; }
  .tu-picker-panel__btn {
    border: 1px solid #dcdcdc;
    line-height: 24px;
    border-radius: 2px;
    padding: 0 20px;
    cursor: pointer;
    background-color: transparent;
    outline: none;
    font-size: 12px; }
    .tu-picker-panel__btn[disabled] {
      color: #cccccc;
      cursor: not-allowed; }
  .tu-picker-panel__icon-btn {
    font-size: 12px;
    color: #9baacf;
    border: 0;
    background: transparent;
    cursor: pointer;
    outline: none;
    margin-top: 8px;
    transition: all 0.3s;
    padding: 0 6px; }
    .tu-picker-panel__icon-btn:hover {
      color: #6d5dfc; }
    .tu-picker-panel__icon-btn.is-disabled {
      color: #c8d0e7; }
      .tu-picker-panel__icon-btn.is-disabled:hover {
        cursor: not-allowed; }
  .tu-picker-panel__link-btn {
    vertical-align: middle;
    margin-left: 8px; }

.tu-picker-panel *[slot="sidebar"],
.tu-picker-panel__sidebar {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 80px;
  border-right: 1px solid rgba(190, 200, 228, 0.38);
  box-sizing: border-box;
  padding-top: 6px;
  overflow: auto; }
  .tu-picker-panel *[slot="sidebar"]::-webkit-scrollbar-track-piece,
  .tu-picker-panel__sidebar::-webkit-scrollbar-track-piece {
    background-color: transparent;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px; }
  .tu-picker-panel *[slot="sidebar"]::-webkit-scrollbar,
  .tu-picker-panel__sidebar::-webkit-scrollbar {
    width: 8px; }
  .tu-picker-panel *[slot="sidebar"]::-webkit-scrollbar-thumb,
  .tu-picker-panel__sidebar::-webkit-scrollbar-thumb {
    background-color: #c8d0e7;
    background-clip: padding-box;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;
    transition: 0.3s ease;
    z-index: 100; }
  .tu-picker-panel *[slot="sidebar"]::-webkit-scrollbar-thumb:hover,
  .tu-picker-panel__sidebar::-webkit-scrollbar-thumb:hover {
    background-color: #bec8e4;
    cursor: pointer; }

.tu-picker-panel *[slot="sidebar"] + .tu-picker-panel__body,
.tu-picker-panel__sidebar + .tu-picker-panel__body {
  margin-left: 80px; }

.tu-time-spinner.has-seconds .tu-time-spinner__wrapper {
  width: 33.3%; }

.tu-time-spinner__wrapper {
  max-height: 190px;
  display: inline-block;
  width: 50%;
  vertical-align: top;
  position: relative; }
  .tu-time-spinner__wrapper .tu-scrollbar__wrap:not(.tu-scrollbar__wrap--hidden-default) {
    padding-bottom: 15px; }
  .tu-time-spinner__wrapper.is-arrow {
    box-sizing: border-box;
    text-align: center;
    overflow: hidden; }
    .tu-time-spinner__wrapper.is-arrow .tu-time-spinner__list {
      transform: translateY(-32px); }
    .tu-time-spinner__wrapper.is-arrow .tu-time-spinner__item:hover:not(.disabled):not(.active) {
      background: rgba(190, 200, 228, 0.38);
      cursor: default; }

.tu-time-spinner__arrow {
  font-size: 12px;
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 1;
  text-align: center;
  height: 30px;
  line-height: 30px;
  color: #bec8e4;
  cursor: pointer;
  transition: all 0.3s; }
  .tu-time-spinner__arrow:hover {
    color: #6d5dfc; }
  .tu-time-spinner__arrow.tu-icon-up {
    top: 10px; }
  .tu-time-spinner__arrow.tu-icon-down {
    bottom: 10px; }

.tu-time-spinner__input.tu-input {
  width: 70%; }
  .tu-time-spinner__input.tu-input .tu-input__inner {
    padding: 0;
    text-align: center; }

.tu-time-spinner__list {
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center; }
  .tu-time-spinner__list::after, .tu-time-spinner__list::before {
    content: "";
    display: block;
    width: 100%;
    height: 80px; }

.tu-time-spinner__item {
  font-size: 14px;
  height: 32px;
  line-height: 32px;
  color: #9baacf;
  transition: all 0.3s; }
  .tu-time-spinner__item:hover:not(.disabled):not(.active) {
    background: rgba(190, 200, 228, 0.38);
    cursor: pointer; }
  .tu-time-spinner__item.active:not(.disabled) {
    color: #6d5dfc; }
  .tu-time-spinner__item.disabled {
    color: #bec8e4;
    cursor: not-allowed; }

.tu-time-range-picker {
  width: 354px;
  overflow: visible;
  color: #9baacf; }
  .tu-time-range-picker__content {
    position: relative;
    text-align: center;
    padding: 10px; }
  .tu-time-range-picker__cell {
    box-sizing: border-box;
    margin: 0;
    padding: 4px 7px 7px;
    width: 50%;
    display: inline-block; }
  .tu-time-range-picker__header {
    margin-bottom: 5px;
    text-align: center;
    font-size: 14px; }
  .tu-time-range-picker__body {
    border-radius: 2px; }
